@import url('https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap');

:root {
  /* HTML Element Default Style Configuration */
  --text-font-family: 'ClashDisplay-Light', system-ui;
  --highlight-text-font-family: 'ClashDisplay-Regular', system-ui;
}

/* HTML Element Default Style Configuration */

html,
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

span,
p,
h1,
h2,
h3,
h4,
h5,
h6,
a {
  color: white;
  font-family: var(--text-font-family);
}

ul,
li {
  list-style-type: none;
}

/* Font Load */

@font-face {
  font-family: 'ClashDisplay-Variable';
  src: url('../../../public/fonts/ClashDisplay-Variable.woff2') format('woff2'),
    url('../../../public/fonts/ClashDisplay-Variable.woff') format('woff'),
    url('../../../public/fonts/ClashDisplay-Variable.ttf') format('truetype');
  font-weight: 200 700;
  font-display: swap;
  font-style: normal;
}


